@font-face {
  font-family: 'dfi';
  src: url('../fonts/dfi.eot?scvxla');
  src: url('../fonts/dfi.eot?scvxla#iefix') format('embedded-opentype'),
    url('../fonts/dfi.ttf?scvxla') format('truetype'),
    url('../fonts/dfi.woff?scvxla') format('woff'),
    url('../fonts/dfi.svg?scvxla#dfi') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="dfi-"],
[class*=" dfi-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'dfi' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  &::before {
    font-family: 'dfi' !important;
  }
}

