.df-cmo {
  &__wrap {
    position: relative;
    justify-content: flex-start;
    align-items: flex-start;
    @extend %flexbox;
  }

  &__lhs,
  &__rhs {
    position: relative;
    height: 100vh;
    min-width: 320px;
    max-width: 550px;
    width: 100%;


  }

  &__lhs {
    background: $client-secondary-color;
    padding: 6px 16px;
  }

  &__rhs {
    position: absolute;
    left: -100%;
    padding-top: 24px;
    @include transition(left .3s ease-in-out);

    &.df-active {
      left: 0;
      background: rgba($client-primary-color, 0.8);
    }

    @media screen and (min-width: $media-mobile-max) {
      position: static;
      padding-top: 0;

      .df-pitch__fix,
      .df-overlay__header {
        display: none;
      }

      &.df-active {
        left: 0;
      }
    }
  }

  &__top {
    min-height: 48px;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox;
  }

  &__bck,
  &__help {
    cursor: pointer;
    height: 24px;
    width: 24px;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    @extend %flexbox;
  }

  &__bck {
    text-align: center;
    color: $white;
  }

  &__help {
    width: 20px;
    height: 20px;
    background: #ffffff;
    color: $client-primary-color;
    justify-content: center;
    align-items: center;
    @include border-radius(100px);
    @extend %flexbox;
  }

  &__status {
    text-align: center;
    flex-basis: 100%;
  }

  &__mat {
    &-num {
      margin-bottom: 4px;
      font-size: 12px;
      color: $light-grey-blue;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;

      @media screen and (min-width: $media-mobile-max) {
        font-size: 14px;
      }
    }

    &-time {
      font-size: 14px;
      color: $white;
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;

      @media screen and (min-width: $media-mobile-max) {
        font-size: 16px;
      }
    }
  }

  // fixture bar
  &__fix {
    padding: 6px 0 16px;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox;

    &-lft {
      ul {
        @extend %flexbox;

        li {
          cursor: pointer;
          text-transform: uppercase;
          font-size: 14px;
          color: $light-grey-blue;
          font-family: $bold_font-family;
          font-weight: $bold_font-weight;
          letter-spacing: 0.18px;

          @media screen and (min-width: $media-mobile-max) {
            font-size: 16px;
          }

          em {
            font-family: $regular_font-family;
            font-weight: $regular_font-weight;
          }

          &:not(:last-child) {
            padding-right: 32px;
          }
        }
      }
    }

    &-rgt {
      cursor: pointer;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
      font-size: 12px;
      color: $light-grey-blue;

      @media screen and (min-width: $media-mobile-max) {
        font-size: 14px;
      }

      i {
        margin-right: 4px;
      }
    }
  }

  // PickedPlayer & ProgressBar
  &__pickPlayer {
    padding-bottom: 12px;

    &-top {
      padding: 0 0 6px 0;
      justify-content: space-between;
      align-items: center;
      @extend %flexbox;
    }

    &-lft {
      text-align: left;

      ul {
        @extend %flexbox;

        li {
          &.df-active {
            .df-cmo__pickPlayer-num {
              color: $white;
            }
          }

          &:not(:last-child) {
            padding-right: 32px;
          }
        }
      }
    }

    &-rgt {
      text-align: right;

      .df-cmo__pickPlayer-num {
        color: $white;
      }
    }

    &-txt {
      font-size: 12px;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
      color: $light-grey-blue;

      @media screen and (min-width: $media-mobile-max) {
        font-size: 14px;
      }
    }

    &-num {
      font-size: 24px;
      color: $light-grey-blue;
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;

      @media screen and (min-width: $media-mobile-max) {
        font-size: 36px;
      }

      em {
        font-size: 12px;
        font-family: $regular_font-family;
        font-weight: $regular_font-weight;
        color: $light-grey-blue;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 14px;
        }
      }

      i {
        color: $light-grey-blue;
        font-size: 12px;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 14px;
        }
      }
    }

    &-pgr {
      overflow: hidden;
      width: 100%;
      height: 24px;
      background: $light-grey-blue;
      box-shadow: inset 0 0 2px 0 rgba($black, 0.45);
      @include border-radius(8px);

      @media screen and (min-width: $media-mobile-max) {
        height: 40px;
      }

      ul {
        justify-content: flex-start;
        align-items: flex-start;
        @extend %flexbox;

        li {
          flex-basis: 29px;
          flex-shrink: 0;
          flex-grow: 1;
          padding: 4px;
          min-height: 24px;
          position: relative;

          @media screen and (min-width: $media-mobile-max) {
            min-height: 40px;
          }

          img {
            display: block;
            margin: 0 auto;
            width: auto;
            height: calc(100% - 4px);
            position: absolute;
            left: 0;
            right: 0;
            top: 50%;
            @include transform(translateY(-50%));

            @media screen and (min-width: $media-mobile-max) {
              height: calc(100% - 8px);
            }
          }

          &.df-active {
            background: $white;
          }
        }
      }
    }
  }

  // tabs
  &__tabs {
    margin-left: -16px;
    margin-right: -16px;
    padding: 0 16px;
    background-color: #ffffff;
    position: relative;
    @include box-shadow(0, 2px, 4px, 0, rgba(1, 1, 6, 0.05));

    ul {
      justify-content: space-between;
      align-items: center;
      @extend %flexbox;

      li {
        padding: 0 16px;
        flex-basis: 100%;
        text-align: center;
        font-size: 14px;
        color: $steel;
        font-family: $bold_font-family;
        font-weight: $bold_font-weight;
        text-transform: uppercase;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 20px;
        }

        em {
          font-size: 12px;
          font-family: $regular_font-family;
          font-weight: $regular_font-weight;

          @media screen and (min-width: $media-mobile-max) {
            font-size: 14px;
          }
        }

        &.df-active {
          color: $client-primary-color;

          span {
            &::after {
              width: 100%;
              background: $client-secondary-color;
            }
          }
        }

        span {
          display: inline-block;
          padding: 8px 4px 12px;
          position: relative;
          cursor: pointer;
          @include transition(color .15s linear);

          &::after {
            content: "";
            position: absolute;
            top: auto;
            bottom: 0;
            left: 0;
            margin: 0 auto;
            width: 0;
            height: 4px;
            @include border-radius(2px);
            @include transition(width .15s linear);
          }
        }
      }
    }
  }

  // info bar
  &__info {
    border-bottom: 1px solid #e7e7e7;
    margin-left: -16px;
    margin-right: -16px;
    padding: 10px 16px;
    background: #f5f5f5;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox;

    &-title {
      font-size: 14px;
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;
      color: $client-primary-color;

      @media screen and (min-width: $media-mobile-max) {
        font-size: 16px;
      }
    }

    &-icon {
      position: relative;
      display: inline-block;
      width: 20px;
      height: 20px;
      text-align: center;
      color: $client-primary-color;
      cursor: pointer;

      &:not(:last-child) {
        margin-right: 20px;
      }

      &.df-active {
        &::after {
          content: "";
          width: 8px;
          height: 8px;
          border: 1px solid $white;
          background: $green;
          position: absolute;
          left: auto;
          top: 0;
          right: 0;
          @include border-radius(100px);
        }
      }
    }
  }

  // footer btn
  &__footer {
    position: fixed;
    max-width: 550px;
    width: 100%;
    left: 0;
    bottom: 0;
    top: auto;
    padding: 16px;

    ul {
      justify-content: center;
      align-items: center;
      @extend %flexbox;

      li {
        padding: 0 6px;
        flex-basis: 100%;
        flex-grow: 1;
      }
    }
  }
}