//popInUp
@include keyframes(df-popInUp) {
  0% {
    @include opacity(0);
    @include transform(translate(0%, 100%));
  }

  100% {
    @include opacity(1);
    transform-origin: 50% 0%;
    @include transform(translate(0%, 0%));
  }
}

//popOutUp
@include keyframes(df-popOutUp) {
  0% {
    @include opacity(1);
    @include transform(translate(0%, 0%));
  }

  100% {
    @include opacity(0);
    @include transform(translate(0%, 100%));
  }
}

//fade
@include keyframes(df-opacity) {
  0% {
    @include opacity(0);
  }

  100% {
    @include opacity(1);
  }
}

//Rotate
@include keyframes(df-rotate) {
  0% {
    @include transform(rotateY(0deg));
  }

  100% {
    @include transform(rotateY(360deg));
  }
}

//toast
@include keyframes(df-toast) {
  0% {
    @include transform(translateY(0%));
  }

  100% {
    @include transform(translateY(100%));
  }
}