// Pick Player List
.df-plyrSel {
  &__wrap {
    overflow: hidden;
    margin-left: -16px;
    margin-right: -16px;
    background: $white;
    height: 100%;

    .df-tbl {
      padding: 0 16px;
      width: 100%;
      table-layout: fixed;

      &__head {
        .df-tbl__cell {
          font-size: 12px;
          font-family: $regular_font-family;
          font-weight: $regular_font-weight;
          color: $steel;

          @media screen and (min-width: $media-mobile-max) {
            font-size: 14px;
          }

          &--thumb,
          &--name,
          &--pts,
          &--amt,
          &--captn {
            cursor: pointer;
          }

          &.df-active {
            &--up {
              span {
                &::after {
                  @include transform(translateY(-50%) rotateX(180deg));
                }
              }
            }

            &--dw {
              span {
                &::after {
                  @include transform(translateY(-50%));
                }
              }
            }

            &--up,
            &--dw {
              span {
                display: inline-block;
                color: $client-primary-color;
                position: relative;

                &::after {
                  content: "\e908";
                  font-family: 'dfi' !important;
                  color: $red;
                  position: absolute;
                  left: auto;
                  right: -16px;
                  top: 50%;
                  width: 12px;
                  height: 12px;
                  justify-content: center;
                  align-items: center;
                  @extend %flexbox;
                }
              }
            }
          }
        }
      }

      &__body {
        .df-tbl__cell {
          &--pts {
            font-size: 14px;
            font-family: $regular_font-family;
            font-weight: $regular_font-weight;
            color: $steel;

            @media screen and (min-width: $media-mobile-max) {
              font-size: 16px;
            }
          }

          &--amt {
            font-size: 14px;
            font-family: $bold_font-family;
            font-weight: $bold_font-weight;
            color: $client-primary-color;

            @media screen and (min-width: $media-mobile-max) {
              font-size: 16px;
            }
          }

          &--captn {
            font-size: 12px;
            font-family: $regular_font-family;
            font-weight: $regular_font-weight;
            color: $steel;

            @media screen and (min-width: $media-mobile-max) {
              font-size: 14px;
            }
          }
        }
      }

      &__cell {
        padding: 0 4px;

        &--thumb {
          width: 72px;
          position: relative;

          @media screen and (min-width: $media-mobile-max) {
            width: 80px;
          }
        }

        &--name {
          width: 100%;
        }

        &--pts {
          width: 60px;

          @media screen and (min-width: $media-mobile-max) {
            width: 72px;
          }
        }

        &--amt {
          width: 60px;

          @media screen and (min-width: $media-mobile-max) {
            width: 72px;
          }
        }

        &--captn {
          width: 60px;
          text-align: center;

          @media screen and (min-width: $media-mobile-max) {
            width: 72px;
          }
        }

        &--cta {
          width: 24px;

          @media screen and (min-width: $media-mobile-max) {
            width: 32px;
          }
        }
      }
    }
  }

  &__top {
    padding: 8px 0;
    border-bottom: 1px solid rgba(#dcdcdc, .6);
  }

  &__list {
    background: $duck-egg-blue;
    overflow-y: auto;
    padding-bottom: 70px;
    height: 100%;

    .df-transfer__tbl .df-tbl {
      padding: 0;
    }

    .df-accordion {
      &__head {
        padding-bottom: 0;

        .df-transfer__fix ul li:not(:last-child) {
          border-bottom: none;
        }
      }
    }

    .df-noDataFound {
      padding: 32px 0;
    }

    ul {
      li {
        background: $white;
        padding: 8px 0;
        cursor: pointer;

        &:not(:last-child) {
          border-bottom: 1px solid rgba(#dcdcdc, .6);
        }

        &.df-remove {
          background: #fffad6;

          .df-plyrSel__cta {
            color: $red;
            border-color: $red;

            &::before,
            &::after {
              background: $red;
            }

            &::after {
              height: 0;
            }
          }
        }

        &.df-injured {
          .df-plyrSel__name {
            span {
              &::after {
                content: "";
                width: 12px;
                height: 12px;
                background: url(../images/injured.svg) center center no-repeat;
                position: absolute;
                left: auto;
                right: 0;
                top: 50%;
                @include transform(translateY(-50%));
                @include background-size(contain);
              }
            }
          }
        }

        &.df-disabled {
          position: relative;
          cursor: not-allowed;

          &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba($white, .4);
          }

          >div {
            opacity: .4;
          }

          .df-plyrSel__cta {
            border-color: rgb(163, 176, 208);

            &::before,
            &::after {
              background: rgb(163, 176, 208);
            }
          }
        }
      }
    }
  }

  &__status {
    position: absolute;
    left: -12px;
    top: auto;
    bottom: -4px;
    cursor: default;

    >div {
      &:not(:last-child) {
        margin-bottom: 4px;
      }
    }
  }

  &__batch {
    overflow: hidden;
    border: 0.5px solid $light-grey-blue;
    justify-content: flex-start;
    align-items: center;
    @extend %inline-flex;
    @include border-radius(100px);
    @include box-shadow(0, 1px, 3px, 0, rgba($black, 0.15));
  }

  &__flight {
    width: 12px;
    height: 12px;
    font-size: 10px;
    text-align: center;
    color: $light-grey-blue;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
    }
  }

  &__logo {
    width: 16px;
    height: 16px;
    background: no-repeat center center;
    @include background-size(contain);

    @media screen and (min-width: $media-mobile-max) {
      width: 20px;
      height: 20px;
    }
  }

  &__team,
  &__skill {
    padding: 2px 4px;
    font-size: 10px;
    font-family: $regular_font-family;
    font-weight: $regular_font-weight;
    color: $client-secondary-color;
    text-transform: uppercase;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
    }
  }

  &__team {
    background: $white;
  }

  &__skill {
    background-color: rgba(222, 230, 246, 0.92);
  }

  &__thumb {
    margin-bottom: -8px;
    height: 64px;
    width: 64px;
    background: no-repeat center bottom;
    @include background-size(contain);

    @media screen and (min-width: $media-mobile-max) {
      height: 72px;
      width: 72px;
    }
  }

  &__name {
    font-size: 14px;
    font-family: $bold_font-family;
    font-weight: $bold_font-weight;
    color: $client-primary-color;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 16px;
    }

    span {
      position: relative;
      display: inline-block;
      padding-right: 16px;
    }
  }

  &__stats {
    font-size: 12px;
    font-family: $regular_font-family;
    font-weight: $regular_font-weight;
    color: $steel;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 14px;
    }
  }

  &__cta {
    width: 20px;
    height: 20px;
    border: 1px solid $green;
    position: relative;
    @include border-radius(100px);

    @media screen and (min-width: $media-mobile-max) {
      width: 24px;
      height: 24px;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      margin: 0 auto;
      background: $green;
      @include transform(translateY(-50%));
      @include transition(all .2s linear);
    }

    &::before {
      width: calc(100% - 8px);
      height: 2px;
    }

    &::after {
      height: calc(100% - 8px);
      width: 2px;
    }

  }

  &__cap {
    margin: 0 auto 4px;
    text-transform: uppercase;
    background: $white;
    color: $steel;
    border: 1px solid $steel;
    width: 36px;
    height: 36px;
    font-size: 14px;
    justify-content: center;
    align-items: center;
    font-family: $bold_font-family;
    font-weight: $bold_font-weight;
    @include border-radius(100px);
    @extend %flexbox;

    &.df-active {
      border-color: $client-primary-color;
      background: $client-secondary-color;
      color: $white;
    }

  }

  &__content {
    padding: 16px;
    background: $duck-egg-blue;
    text-align: center;

    >div {
      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }

    &-txt {
      &One {
        font-size: 14px;
        font-family: $bold_font-family;
        font-weight: $bold_font-weight;
        color: $client-primary-color;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 16px;
        }
      }

      &Two,
      &Three {
        font-size: 12px;
        font-family: $regular_font-family;
        font-weight: $regular_font-weight;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 14px;
        }
      }

      &Two {
        color: $steel;
      }

      &Three {
        color: $client-primary-color;
      }
    }
  }
}

// overlays
.df-overlay {
  &__wrap {
    z-index: -9999;
    padding-top: 24px;
    background: rgba($client-primary-color, .8);
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    &.df-active {
      .df-toast__wrap {
        opacity: 1;
        @include transform(translateY(0%));
      }

      .df-overlay__container {
        margin-top: -4px;
        @include transition(margin-top .1s linear);
      }
    }
  }

  &__container {
    margin-top: -8px;
    position: relative;
    z-index: 999999;
    min-height: 100%;
    background-color: $duck-egg-blue;
    flex-direction: column;
    @include border-radius(8px 8px 0 0);
    @extend %flexbox;

    &.df-overlay {
      &--alMat {}

      &--search {
        background: $white;

        .df-overlay__body {
          padding: 0 16px;
        }
      }

      &--filter {
        .df-overlay__body {
          background: transparent;
        }

        .df-overlay__cta {
          cursor: pointer;
          position: absolute;
          left: 16px;
          text-transform: uppercase;
          color: $client-secondary-color;
          font-size: 14px;
          font-family: $bold_font-family;
          font-weight: $bold_font-weight;
        }
      }

      &--favTeam {
        .df-overlay__header {
          border-bottom: 1px solid #e7e7e7;
        }

        .df-overlay__body {
          background: transparent;
          padding: 16px 0;
        }
      }

      &--transfer {
        .df-overlay__body {
          background: transparent;
        }

        &-toast {
          position: absolute;
          width: 100%;
          min-height: auto;
          top: auto;
          bottom: 0;
          margin: 0;

          .df-overlay__header {
            border-bottom: 1px solid #e7e7e7;
          }

          .df-overlay__body {
            padding-top: 16px;
            padding-bottom: 70px;
          }
        }
      }

      &--player {
        background: url(../images/overlay-bg.png) no-repeat top right $client-primary-color;
        @include background-size(250px);

        .df-overlay__body {
          background: $duck-egg-blue;
        }

        .df-overlay__close,
        .df-overlay__title {
          color: $white;
        }

        .df-overlay__foot ul li {
          flex-basis: auto;
          flex-grow: 0;
        }

        .df-overlay__cta {
          position: absolute;
          left: 0;
          top: 50%;
          @include transform(translateY(-50%));
        }
      }
    }
  }

  &__title {
    text-align: center;
    color: $client-primary-color;
    font-size: 16px;
    font-family: $bold_font-family;
    font-weight: $bold_font-weight;
  }

  &__close {
    font-size: 20px;
    cursor: pointer;
    width: 24px;
    height: 24px;
    position: absolute;
    top: 50%;
    right: 16px;
    justify-content: center;
    align-items: center;
    color: $client-primary-color;
    @extend %flexbox;
    @include transform(translateY(-50%));
  }

  &__header {
    position: relative;
    padding: 16px;
    font-size: 16px;
  }

  &__body {
    flex-basis: 100%;
    flex-grow: 1;
    background: $white;
  }

  &__foot {
    position: fixed;
    max-width: 550px;
    width: 100%;
    left: 0;
    bottom: 0;
    top: auto;
    padding: 16px;
    z-index: 9999999;

    ul {
      justify-content: center;
      align-items: center;
      @extend %flexbox;

      li {
        padding: 0 6px;
        flex-basis: 100%;
        flex-grow: 1;
      }
    }
  }
}

// All Match Overlay List
.df-alMat {
  &__wrap {}

  &__list {
    overflow-y: auto;

    ul {
      li {
        padding: 16px;
        background: $white;

        &:not(:last-child) {
          border-bottom: 1px solid rgba(#dcdcdc, .6);
        }
      }
    }
  }

  &__box {
    align-items: center;
    justify-content: flex-end;
    @extend %flexbox;

    &.df-success {
      color: $dark-aquamarine;
    }

    &.df-danger {
      color: $rust-red;
    }
  }

  &__lft {
    flex-basis: 60%;
  }

  &__rgt {
    flex-basis: 40%;
  }

  &__type,
  &__date {
    text-align: right;
    font-size: 12px;
    font-family: $regular_font-family;
    font-weight: $regular_font-weight;
  }

  &__type {
    margin-bottom: 4px;
    color: $client-primary-color;
  }

  &__date {
    color: $steel;
  }

  &__status {
    margin-top: 4px;
    text-align: right;
    font-size: 12px;
    font-family: $bold_font-family;
    font-weight: $bold_font-weight;
  }

  &__result {
    margin-top: 4px;
    font-size: 12px;
    font-family: $regular_font-family;
    font-weight: $regular_font-weight;
  }

}

// Fixtures
.df-fix {
  &__team {
    align-items: center;
    @extend %flexbox;

    &-A,
    &-B {
      align-items: center;
      @extend %flexbox;
    }

    &-vs {
      flex-basis: 50px;
      flex-shrink: 0;
      text-align: center;
      font-size: 12px;
      color: $steel;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;

    }

    &-name {
      margin: 0px 4px;
      font-size: 14px;
      text-transform: uppercase;
      color: $client-primary-color;
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;
    }

    &-logo {
      width: 24px;
      height: 24px;
      background-repeat: no-repeat;
      background-position: center center;
      @include background-size(contain);
    }
  }
}

// search bar
.df__searchBar {
  justify-content: space-between;
  align-items: center;
  @extend %flexbox;

  &-lft {
    flex-basis: 100%;
  }

  &-rgt {
    padding-left: 16px;
  }

  &-cta {
    cursor: pointer;
    text-transform: uppercase;
    font-size: 14px;
    font-family: $bold_font-family;
    font-weight: $bold_font-weight;
    color: $client-primary-color;
  }
}

// Filter 
.df-filter {
  &__wrap {
    border-top: 1px solid #e7e7e7;
    justify-content: space-between;
    align-items: flex-start;
    @extend %flexbox;
  }

  &__lft {
    width: 120px;
    background: $white;
    flex-shrink: 0;
    height: 100vh;
  }

  &__rgt {
    padding: 0 12px;
    flex-basis: 100%;
    background: $duck-egg-blue;

    ul {
      li {
        padding: 16px 0;
        border-bottom: 1px solid #e7e7e7;
      }
    }
  }

  &__title {
    cursor: pointer;
    padding: 16px;
    text-align: left;
    font-size: 14px;
    color: $steel;
    font-family: $bold_font-family;
    font-weight: $bold_font-weight;

    &.df-active {
      span {
        position: relative;
        display: inline-block;
        padding-right: 8px;

        &::after {
          content: "";
          width: 4px;
          height: 4px;
          background: $green;
          position: absolute;
          left: auto;
          top: 0;
          right: 0;
          @include border-radius(100px);
        }
      }
    }

    &.df-selected {
      color: $client-secondary-color;
      background: $duck-egg-blue;
    }
  }

  &__subTxt {
    display: block;
    font-size: 12px;

    span {
      &:not(:last-child) {
        padding-right: 8px;
      }
    }
  }
}

// toast msg
.df-toast {
  &__wrap {
    position: relative;
    top: 0;
    width: 100%;
    z-index: 999999;
    margin-top: -24px;
    padding: 8px 8px 16px;
    color: $white;
    text-align: center;
    font-size: 12px;
    opacity: 0;
    @include transform(translateY(100%));
    @include transition(transform .1s linear);

    &.df-success {
      background: $green;
    }

    &.df-danger {
      background: $red;
    }
  }

  &__title {
    font-family: $bold_font-family;
    font-weight: $bold_font-weight;
  }

  &__text {
    font-family: $regular_font-family;
    font-weight: $regular_font-weight;
  }
}

// Fav team
.df-favTeam {
  &__wrap {}

  &__txt {
    text-align: center;
    font-size: 12px;
    font-family: $regular_font-family;
    font-weight: $regular_font-weight;
    color: $client-primary-color;
  }

  &__list {
    padding: 20px 0;

    ul {
      li {
        cursor: pointer;
        margin-bottom: 12px;

        &.df-active {
          .df-favTeam__cta {
            border-color: $client-secondary-color;

            &::before {
              background: $client-secondary-color;
            }
          }
        }
      }
    }
  }

  &__box {
    padding: 16px;
    background: $white;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox;
    @include box-shadow(0, 2px, 4px, 0, rgba(1, 1, 6, 0.05));

    &-lft {
      flex-basis: 100%;
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox;
    }

    &-rgt {
      flex-basis: 50px;
    }
  }

  &__thumb {
    flex-shrink: 0;
    width: 48px;
    height: 48px;
    background: center center no-repeat;
    @include background-size(contain);
  }

  &__team {
    padding-left: 16px;
    flex-basis: 100%;

    &-name {
      font-size: 16px;
      color: $client-primary-color;
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;
      margin-bottom: 4px;
    }

    &-stats {
      font-size: 14px;
      color: $steel;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
    }
  }

  &__cta {
    position: relative;
    width: 24px;
    height: 24px;
    background: $white;
    border: 1px solid $steel;
    justify-content: center;
    align-items: center;
    @include border-radius(100px);
    @extend %flexbox;

    &::before {
      content: "";
      position: absolute;
      width: calc(100% - 10px);
      height: calc(100% - 10px);
      @include border-radius(100px);
    }
  }
}

// Transfer
.df-transfer {
  &__wrap {}

  &__info {
    color: $white;
    margin: 0 -16px;
    align-items: center;
    justify-content: space-between;
    @extend %flexbox;

    &.df-success {
      background: $green;

      .df-transfer__info-lft {
        background: $dark-aquamarine;
      }
    }

    &.df-danger {
      background: $red;

      .df-transfer__info-lft {
        background: $dark-red;
      }
    }

    &.df-active {
      .df-transfer__info-lft {
        margin-left: 0;
      }

      .df-transfer__info-rgt {
        font-size: 0;

        i {
          font-size: 16px;
          margin-right: 12px;
        }
      }
    }

    &-lft,
    &-mid,
    &-rgt {
      font-size: 14px;
      padding: 12px 16px;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;

      @media screen and (min-width: $media-mobile-max) {
        font-size: 16px;
      }

      em {
        margin-right: 4px;
        font-family: $bold_font-family;
        font-weight: $bold_font-weight;
      }
    }

    &-lft,
    &-rgt {
      span {
        position: relative;
        justify-content: flex-start;
        align-items: center;
        @extend %flexbox;

        i {
          width: 20px;
          height: 20px;
          justify-content: center;
          align-items: center;
          @extend %flexbox;
        }
      }
    }

    &-lft {
      margin-left: -140px;
      @include border-radius(0px 100px 100px 0px);
      @include transition(margin-left .3s linear);

      @media screen and (min-width: $media-mobile-max) {
        margin-left: -155px;
      }

      span {

        i {
          margin-left: 12px;
          cursor: pointer;
          border: 1px solid $white;
          @include border-radius(100px);

          &::after {
            content: "";
            width: calc(100% - 8px);
            height: 2px;
            background: $white;
          }
        }
      }
    }

    &-mid {
      flex-grow: 1;
    }

    &-rgt {
      cursor: pointer;
      white-space: nowrap;
      text-align: right;

      span {
        position: relative;
        justify-content: flex-start;
        align-items: center;
        @extend %flexbox;

        i {
          margin-right: 12px;

          &::after {
            content: "\e90c";
            font-family: 'dfi' !important;
            font-style: normal;
          }
        }
      }
    }
  }

  &__head {
    font-size: 14px;
    background: $white;
    padding: 10px 16px;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox;
    @include box-shadow(0, 2px, 4px, 0, rgba(1, 1, 6, 0.05));

    span {
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
      color: $client-primary-color;

      em {
        font-family: $bold_font-family;
        font-weight: $bold_font-weight;
      }
    }
  }

  &__status {
    margin-bottom: 12px;
    font-size: 14px;
    color: $client-primary-color;
    font-family: $regular_font-family;
    font-weight: $regular_font-weight;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox;

    em {
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;
    }
  }

  &__fix {
    ul {
      @extend %flexbox;

      li {
        cursor: pointer;
        text-transform: uppercase;
        font-size: 14px;
        color: $light-grey-blue;
        font-family: $bold_font-family;
        font-weight: $bold_font-weight;
        letter-spacing: 0.18px;

        em {
          font-family: $regular_font-family;
          font-weight: $regular_font-weight;
        }

        &:not(:last-child) {
          padding-right: 32px;
        }
      }
    }
  }

  &__tbl {
    .df-tbl {
      width: 100%;
      table-layout: fixed;

      &__head {

        .df-tbl__cell {
          border-bottom: 1px solid rgba(#dcdcdc, .6);
          font-size: 12px;
          font-family: $regular_font-family;
          font-weight: $regular_font-weight;
          color: $steel;
          padding-bottom: 12px;
        }
      }

      &__body {
        .df-tbl__cell {
          width: 50%;
          padding: 0;
        }
      }

      &__cell {
        &--lft {
          i {
            padding: 0 8px;
          }

          .df-transfer__plyr {
            margin-right: 16px;
          }
        }

        &--rgt {
          text-align: right;

          i {
            padding: 0 8px;
            color: $dark-aquamarine;
          }

          .df-transfer__plyr {
            margin-left: 16px;
            flex-direction: row-reverse;

            &-name {
              color: $dark-aquamarine;
            }
          }
        }
      }
    }
  }

  &__plyr {
    margin-top: 8px;
    margin-bottom: 8px;
    border-bottom: 1px solid #e4e4e4;
    justify-content: flex-start;
    align-items: center;
    @extend %flexbox;

    &-thumb {
      height: 48px;
      width: 48px;
      background: no-repeat center bottom;
      @include background-size(contain);
    }

    &-dtls {
      padding: 0 4px;
    }

    &-name {
      color: $steel;
      font-size: 12px;
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;
    }

    &-skill {
      font-size: 10px;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
      color: $client-primary-color;
    }
  }

  &__icon {
    position: relative;
    color: $client-primary-color;
    width: 80px;
    height: 80px;
    margin: 0 auto 16px;
    border: 1px solid $client-primary-color;
    @include border-radius(100px);

    &::after {
      content: "\e90b";
      font-family: 'dfi' !important;
      height: 100%;
      width: 100%;
      font-size: 36px;
      justify-content: center;
      align-items: center;
      @extend %flexbox;

    }
  }

  &__content {
    text-align: center;
    padding-bottom: 20px;
    font-size: 14px;
    color: $steel;
    font-family: $regular_font-family;
    font-weight: $regular_font-weight;

    span {
      display: block;
    }
  }
}

// Player Info Card
.df-player {
  &__head {
    padding: 12px 16px 0;
    justify-content: flex-start;
    align-items: center;
    @extend %flexbox;
  }

  &__thumb {
    width: 80px;
    height: 80px;
    flex-shrink: 0;
    background: no-repeat center top;
    @include background-size(contain);
  }

  &__stats {
    flex-basis: 100%;

    ul {
      padding-left: 40px;
      justify-content: flex-start;
      align-items: flex-start;
      @extend %flexbox;

      li {
        flex-grow: 1;
        flex-basis: 100%;
      }
    }

    &-txt {
      font-size: 12px;
      color: $steel;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
    }

    &-num {
      font-size: 24px;
      color: $white;
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;
    }
  }

  &__body {}

  &__profile {
    background: $white;
    padding: 16px;
    border-bottom: 1px solid rgba(#dcdcdc, .6);

    &-row {
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox;

      &:not(:last-child) {
        margin-bottom: 6px;
      }
    }

    &-name {
      flex-basis: 100%;
      font-size: 20px;
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;
      color: $client-primary-color;
    }

    &-cta {
      cursor: pointer;
      flex-basis: 100%;
      text-align: right;
      font-size: 12px;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
      color: $client-secondary-color;

      i {
        margin-left: 4px;
        font-size: 10px;
      }
    }


    &-icon,
    &-logo {
      width: 16px;
      height: 16px;
      margin-right: 4px;
    }

    &-logo {
      background: no-repeat center center;
      @include background-size(contain);
    }

    &-skill {
      font-size: 12px;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;

      span {
        color: $steel;

        &:not(:last-child) {
          color: $client-primary-color;
        }
      }
    }

    &-icon {
      color: $steel;
      font-size: 12px;
      text-align: center;
    }

    &-txt {
      font-size: 14px;
      color: $steel;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
    }
  }

  &__tbl {
    margin: 12px 0;

    .df-tbl {
      width: 100%;
      table-layout: fixed;

      &__head {
        .df-tbl__cell {
          font-size: 12px;
          font-family: $regular_font-family;
          font-weight: $regular_font-weight;
          color: $steel;
        }
      }

      &__body {
        .df-tbl__cell {
          font-size: 14px;
          font-family: $regular_font-family;
          font-weight: $regular_font-weight;
          color: $client-primary-color;
        }

        .df-tbl__row {
          .df-tbl__cell {
            border-top: 1px solid #e7e7e7;
          }
        }
      }

      &__cell {
        padding: 8px 0px;

        &--evt {}

        &--act {
          text-align: center;
        }

        &--pts {
          text-align: right;
        }
      }
    }
  }

  &--complete {
    .df-accordion {
      margin: 0;

      &__box {
        padding: 0;
      }

      &__head {
        padding-bottom: 0;

        &::after {
          bottom: 4px;
          right: 16px;
        }
      }

      &__body {
        border-top: none;
        padding: 0 16px;

        .df-player__tbl {
          margin-top: 0;
        }
      }
    }

    .df-alMat {
      &__box {
        align-items: flex-start;
        flex-direction: column;
      }

      &__lft {
        flex-basis: 100%;
        margin-bottom: 8px;
      }

      &__rgt {
        flex-basis: 100%;
        justify-content: flex-start;
        align-items: center;
        @extend %flexbox;
      }

      &__type {
        margin-bottom: 0;
        margin-right: 8px;
      }
    }

    .df-player {
      &__fBox {
        justify-content: flex-start;
        align-items: center;
        @extend %flexbox;

        &--head {
          background: $white;
          font-size: 12px;
          padding: 8px 16px;
          border-bottom: 1px solid #e7e7e7;
          font-family: $regular_font-family;
          font-weight: $regular_font-weight;
          color: $steel;
        }

        &--body {
          padding: 8px 16px;
          align-items: flex-start;

          .df-player__fItem--cent,
          .df-player__fItem--pts {
            font-size: 14px;
            font-family: $bold_font-family;
            font-weight: $bold_font-weight;
            color: $client-primary-color;
          }
        }
      }

      &__fItem {
        &--mat {
          flex-basis: 100%;
        }

        &--cent,
        &--pts {
          flex-basis: 120px;
        }

        &--cent {
          text-align: center;
        }

        &--pts {
          text-align: right;
        }
      }
    }
  }
}

// Pitch View
.df-pitch {
  &__wrap {
    height: 100%;
    min-height: 100%;
    background: url(../images/pitch.jpg) no-repeat center center;
    flex-direction: column;
    overflow: hidden;
    @include background-size(cover);
    @extend %flexbox;
    @include border-radius(8px 8px 0px 0px);

    @media screen and (min-width: $media-mobile-max) {
      @include border-radius(0px);
    }

    .df-overlay {
      &__header {
        background: $client-primary-color;
      }

      &__close,
      &__title {
        color: $white;
      }
    }
  }

  &__area {
    position: relative;
    height: 100%;
    overflow: auto;
  }

  &__box {
    position: absolute;
    top: 0;
    left: 0;
    min-height: 100%;
    width: 100%;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 30px 0 0;
    @extend %flexbox;
  }

  &__row {
    width: 100%;

    &.df-view {
      &--1 {}

      &--2 {}

      &--3 {}

      &--4 {}

      &--5,
      &--6 {
        .df-pitch__card {
          flex-wrap: wrap;
        }
      }

      &--5 {
        .df-pitch__card {
          .df-pitch__plyr {
            flex-basis: 33.33%;
          }
        }
      }

      &--6 {
        .df-pitch__card {
          .df-pitch__plyr {
            flex-basis: 25%;
          }
        }
      }
    }
  }

  &__title {
    text-transform: uppercase;
    text-align: center;
    font-size: 10px;
    color: rgba($white, .4);
    font-family: $regular_font-family;
    font-weight: $regular_font-weight;
    margin-bottom: 8px;
  }

  &__card {
    padding: 8px 0;
    justify-content: space-evenly;
    align-items: center;
    @extend %flexbox;
  }

  &__plyr {
    position: relative;
    text-align: center;

    &.df-cap {
      .df-pitch__tag {
        visibility: visible;

        &::after {
          content: "C";
        }
      }
    }

    &.df-vcap {
      .df-pitch__tag {
        visibility: visible;

        &::after {
          content: "VC";
        }
      }
    }

    &.df-flight {
      .df-pitch__flight {
        visibility: visible;
      }
    }

    &.df-in,
    &.df-out {
      .df-pitch__plyr-name {
        padding-left: 12px;
        padding-right: 12px;

        span {
          &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 8px;
            height: 8px;
            border: 1px solid $white;
            @include border-radius(100px);
          }
        }
      }
    }

    &.df-in {
      .df-pitch__plyr-name {
        span {
          &::after {
            background: $green;
          }
        }
      }
    }

    &.df-out {
      .df-pitch__plyr-name {
        span {
          &::after {
            background: $red;
          }
        }
      }
    }

    &-box {
      display: inline-block;
      width: 100px;
    }

    &-icon {
      position: absolute;
      left: -16px;
      top: auto;
      bottom: 0;
      height: 100%;
      font-size: 10px;
      text-align: center;
      color: $light-grey-blue;
    }

    &-thumb {
      position: relative;
      margin: 0 auto;
      width: 40px;
      height: 40px;
      background: no-repeat center top;
      @include background-size(contain);
    }

    &-name {
      padding: 4px 8px;
      font-size: 12px;
      color: $white;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;

      span {
        position: relative;
        padding: 4px 8px;
        background: $client-primary-color;
        @include text-truncate;
        @include border-radius(100px);
      }
    }

    &-num {
      font-size: 12px;
      color: $white;
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;
    }

  }

  &__tag {
    visibility: hidden;
    margin-top: -4px;
    margin-bottom: 8px;
    width: 20px;
    height: 20px;
    background: $white;
    @include border-radius(100px);

    &::after {
      text-transform: uppercase;
      color: $client-primary-color;
      font-size: 10px;
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;
      height: 100%;
      justify-content: center;
      align-items: center;
      @extend %flexbox;
    }
  }

  &__flight {
    visibility: hidden;
  }

  &__fix {
    padding: 16px;
    background: rgba(#31353e, .4);
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    color: $white;
    @extend %flexbox;

    &-lft {
      font-family: $bold_font-family;
      font-weight: $bold_font-weight;

      em {
        margin: 0 4px;
        font-family: $regular_font-family;
        font-weight: $regular_font-weight;
      }
    }

    &-rgt {
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
    }
  }
}